import MyPagination from "../../../components/MyPagination";
import mixins from "../../../utils/mixins";
import {common} from "../../../utils/api/url";
import {MessageError, MessageSuccess} from "../../../utils/utils";
import LayoutTb from "../../../components/Layout/LayoutTb";
import MySelect from "../../../components/MySelect";
import MyUserType from "../../../components/utils/MyUserType";
import MyDatePicker from "../../../components/MyDatePicker";
import MyInputButton from "../../../components/search/MyInputButton";
import DialogNormal from "../../../components/utils/dialog/DialogNormal";
import MyNormalDialog from "../../../components/utils/dialog/MyNormalDialog";
import creditObjectValidater from "../../../utils/validater/creditObjectValidater";
export default {
  mixins: [mixins, creditObjectValidater],
  components: {MyPagination,LayoutTb,MySelect,MyUserType,MyDatePicker,MyInputButton,DialogNormal,MyNormalDialog},
  data(){
    return {
      tableData: [],
      searchDate: [],
      typeList: [],
      searchKey: '',
      visible: false,
      modalVisible: false,
      dialogLoading: false,
      visibleConfim: false,
      clearTime: '',
      action: '',
      subTitle: '',
      form: {
        id: '',
        type: '',
        socreName: '',
        socreNameSub: '',
        qualityScore: '',
        qualityNfScore: '',
        qualityMnfScore: ''
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    init(){
      let params = {
        page: this.page,
        num: this.num
      };
      this.$axios.get(common.audit_credit_type_page, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    addInfo(){
      this.modalVisible = true;
    },
    search(data){
      this.searchKey = data.input;
      this.page = 1;
      this.init();
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    handleSelect(data){
      this.form.type = data;
    },
    deleteInfo(row){
      this.form.id = row.id;
      this.subTitle = row.socre_name;
      this.visibleConfim = true;
    },
    handleOkChange(data) {
      this.dialogLoading = true;
      let url = "";
      let params = {
        id: this.form.id
      };
      url = common.audit_credit_type_fliter_del;
      params = this.$qs.stringify(params);
      this.$axios.post(url, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.visibleConfim = false;
        this.dialogLoading = false;
      });
    },
    handleCancelChange(data) {
      this.visibleConfim = false;
    },
    cancelDialog(){
      this.modalVisible = false;
    },
    closeDialog(event){
      this.form = {
        id: '',
        type: '',
        socreName: '',
        socreNameSub: '',
        qualityScore: '',
        qualityNfScore: '',
        qualityMnfScore: ''
      };
      this.subTitle = "";
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
    },
    okDialog(event){
      let url = "";
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            socreType: this.form.type == 'true' ? '加分' : "减分",
            socreName: this.form.socreName,
            socreNameSub: this.form.socreNameSub,
            qualityScore: this.form.qualityScore,
          };
          url = common.audit_credit_type_fliter_add;
          params = this.$qs.stringify(params);
          this.$axios.post(url, params, {loading: false}).then(res => {
            if (res.data.code == 200){
              this.modalVisible = false;
              this.init();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    }
  }
}
